import { SyncHook, AsyncParallelHook, AsyncSeriesHook } from 'tapable'

function logger (name) {
  return (...rest) => {
    console.log(`%c${name}`, 'color: blue;', ...rest)
  }
}

/**
 * 
 * SyncHook: 
 * SyncBailHook: SyncHook**undefined**
 * SyncWaterfallHook: 
 * SyncLoopHook: SyncBailHook, **undefined**
 */
;(function () {
  const log = logger('Sync Hook')
  const hook = new SyncHook(['name'])

  hook.tap('hello', name => {
    log(`hello, ${name}`)
  })

  hook.tap('hello again', name => {
    log(`hello, ${name}, again`)
  })

  hook.call('sky')
  hook.call('lee')
})()

/**
 * 
 * AsyncParallelHook: 
 * AsyncSeriesHook: 
 * AsyncParallelBailHook: undefinedcallAsyncPromise
 * AsyncSeriesBailHook: undefinedcallAsyncPromise
 * AsyncSeriesWaterfallHook: SyncWaterfallHook
 */
;(function () {
  const log = logger('Async Parallel Hook')
  const hook = new AsyncParallelHook(['name'])

  hook.tapAsync('hello', (name, done) => {
    setTimeout(() => {
      log(`hello ${name}`)
      done()
    }, 2000)
  })

  hook.tapPromise('hello again', name => {
    return new Promise(resolve => {
      setTimeout(() => {
        log(`hello ${name}, again`)
        resolve()
      }, 1000)
    })
  })

  hook.callAsync('sky', () => {
    log('async parallel hook done!')
  })
})()

;(function () {
  const log = logger('Async Series Hook')
  const hook = new AsyncSeriesHook(['name'])

  hook.tapAsync('hello', (name, done) => {
    setTimeout(() => {
      log(`hello ${name}`)
      done()
    }, 2000)
  })

  hook.tapPromise('hello again', name => {
    return new Promise(resolve => {
      setTimeout(() => {
        log(`hello ${name}, again`)
        resolve()
      }, 1000)
    })
  })

  hook.callAsync('sky', () => {
    log('async series hook done!')
  })
})()
